import React from 'react';

const Dashboard = ({xc}) => {
    console.log('render dash')
        
    return (
        <>
            <h1>Dashboard</h1>
            <button onClick={()=>xc.get_all_tasks()}>get_all_tasks()</button>
            <button onClick={()=>xc.get_started_tasks()}>get_started_tasks()</button>
            <br/>
            <button onClick={()=>xc.add_task({due: 1})}>set_task()</button>
        </>
    )
}

export default Dashboard;